<template>
  <v-chart class="chart" :option="option" />
</template>

<script>
import VChart, { THEME_KEY } from "vue-echarts";
import 'echarts'

export default {
  name: "TradeChart",
  props: {
    chartData : {
      type : Array,
    },
    chartCategory : {
      type : Array,
    },
    chartTitle : {
      type : String,
    }
  },
  components: {
    VChart
  },
  provide: {
    [THEME_KEY]: "dark"
  },
  data() {
    return {
      option: {
        title: {
          text: this.chartTitle,
          left: "center"
        },
        legend: {
          data : this.chartCategory
        },
        tooltip: {
          formatter:function(obj) {
            let value = obj.value;
            return '<div style="border-bottom: 1px solid rgba(255,255,255,.3); font-size: 18px;padding-bottom: 7px;margin-bottom: 7px">'
                + '- 거래일자 : ' + value[0] +'<br />'
                + '- 거래금액 ：' + value[1] + '<br/>'
                + '- 층  ：' + value[2] + '<br/>'
                + '- 면적 ：' + value[3] + '<br/>'
                + '- 거래구분 ：' + value[4] + '<br/>'
                + '- 보증금 ：' + value[5] + '<br/>'
                + '- 월세 ：' + value[6] + '<br/>'
                + '</div>';
          } ,
          axisPointer: {
            type: 'cross'
          }
        },
        xAxis: {
          // type: 'category',
          name: '거래일자',
          scale: true,
          type: 'time',
          distribution: 'linear',
          time: {
            unit: 'day'
          },
          splitLine: {
            lineStyle: {
              type: 'dashed'
            }
          },
          splitNumber: 10,
          nameTextStyle:{
            fontSize : 10
          }
        },
        yAxis: {
          type:'value',
          name:'거래금액',
          splitLine: {
            lineStyle: {
              type: 'dashed'
            }
          },
          nameTextStyle:{
            fontSize : 10
          }
        },
        series: this.chartData
      }
    };
  },
  watch: {
    chartData: {
      handler() {
        this.option.series = this.chartData;
      },
      deep: true
    }
  }
};
</script>

<style scoped>
.echarts {
  width: 100%;
  height: 700px;
}
</style>